













































import uniPopup from '../../../components/uni-popup.vue';
export default {
	data() {
		return {
			showPassword: true,
			showClearIcon: false,
			inputClearValue: "",
			loading:false,
			popType: 'middle',
			tiptitle: '提示',
			showPopupMiddle: false,
			tipcontent:'',
			is_sms:true,
			Loop:'',
			downtime:60,
			verifyCode:'',
			smsCode:'',
			fsbtn:'发送验证码'
		}
	},
	onLoad() {

	},
	onPullDownRefresh() {
	},
	onShareAppMessage() {
		return {
			title: this.webshareTitle,
			path: '/pages/tabBar/index/index'
		}
	},
	onNavigationBarButtonTap(e) {
	},
	methods: {
		goDetailPage(url) {
			uni.navigateTo({
				url: url
			})
		},
		//统一的关闭popup方法
		hidePopup: function() {
			this.showPopupMiddle = false;
			this.showPopupTop = false;
			this.showPopupBottom = false;
		},
		//展示居中 popup
		showMiddlePopup: function() {
			this.hidePopup();
			this.popType = 'middle';
			this.showPopupMiddle = true;
		},
		changePassword: function () {
			this.showPassword = !this.showPassword;
		},
		bindClearInput: function (e) {
			this.inputClearValue = e.target.value;
			if (e.target.value.length > 0) {
				this.showClearIcon = true;
			} else {
				this.showClearIcon = false;
			}
		},
		clearIcon: function () {
			this.inputClearValue = "";
			this.showClearIcon = false;
		},
		formSubmit: function (e) {
			const phone = e.detail.value.account;
			const password = e.detail.value.password;
			const smsCode = e.detail.value.smsCode;
			const verifyCode = this.verifyCode;
			const shareId = uni.getStorageSync('shareId');
			const agentId = uni.getStorageSync('agentId');
			const qdId = uni.getStorageSync('qdId');
			if(phone ==='' ){
				uni.showModal({
					content:'请填写手机号',
					showCancel:false
				})
				return false;
			}
			
			if(smsCode ==='' ){
				uni.showModal({
					content:'请填写验证码',
					showCancel:false
				})
				return false;
			}
			
			if(verifyCode ==='' ){
				uni.showModal({
					content:'请先获取验证码',
					showCancel:false
				})
				return false;
			}
			
			if(password ==='' ){
				uni.showModal({
					content:'请填写6~18位字符新密码',
					showCancel:false,
				})
				return false;
			}
			const that = this;
			try {
				this.$http.post(this.websiteUrl+'/api/api/reg',{phone,password,smsCode,verifyCode,shareId,agentId,qdId},(res) => {
					if(res.data.code==1){
						uni.showModal({
							content:res.data.msg,
							showCancel:false,
							success:()=>{
								this.$store.dispatch('apiLogin',{openid:res.data.result.token,sessionKey:res.data.result.session_id});
								uni.switchTab({
									url: '/pages/tabBar/user/user'
								});
							}
						})
						
						//uni.navigateBack();
					}else{
						uni.showModal({
							content:res.data.msg,
							showCancel:false,
							success:function() {
								that.loading = false;
							}
						})
					}
				});
			} catch (e) {
				uni.showModal({
					content:'网络异常,请稍后重试...',
					showCancel:false,
					success:function() {
						that.loading = false;
					}
				})
			}
			
			//console.log('form发生了submit事件，携带数据为：' + JSON.stringify(e.detail.value))
		},
		getClause() {
			try {
				this.$http.post(this.websiteUrl+'/api/api/getClause',{},(res) => {
					if(res.data.code==1){
						this.tiptitle = '隐私条款';
						this.tipcontent = res.data.result.article.content.replace(/\\/g, "").replace(/<img/g, "<img style=\"display:none;\"");
						this.showMiddlePopup();
					}else{
						uni.showModal({
							content:res.data.msg,
							showCancel:false
						})
					}
				});
			} catch (e) {
				uni.showModal({
					content:'网络异常,请稍后重试...',
					showCancel:false
				})
			}
		},
		sendSMS: function (e) {
			if(this.is_sms){
				this.is_sms = false;
				const phone = this.inputClearValue;
				if(phone ==='' ){
					uni.showModal({
						content:'请填写手机号',
						showCancel:false
					})
					return false;
				}
				try {
					this.$http.post(this.websiteUrl + '/api/api/sendSMS', {
						phone
					}, (res) => {
						if (res.data.code == 1) {
							this.downtime =60;
							this.dateDown();
							this.verifyCode = res.data.result;
						}else{
							uni.showModal({
								content: res.data.msg,
								showCancel: false
							})
							this.is_sms = true;
						}
					});
				} catch (e) {
					uni.showModal({
						content: '网络异常,请稍后重试...',
						showCancel: false
					})
					this.is_sms = true;
				}	
			}	
		},
		dateDown: function () {
			const that = this;
			this.fsbtn='重新发送('+this.downtime+')';
			this.Loop = setInterval(function() {
				if(that.downtime == 0){
					that.fsbtn='发送验证码';
					that.is_sms = true;
					clearInterval(that.Loop);
					return;
				}
				that.downtime--;
				that.fsbtn='重新发送('+this.downtime+')';
			}.bind(this), 1000);
			if(this.is_sms){
				clearInterval(this.Loop);
			}
		}
	},
	components: {
		uniPopup
	}
}
